<template>
<div>
<p v-if="id==0">发布到1.1 (<span>{{num}}</span>/<span>100</span>)</p>
<p v-if="id!=0">回复    <span>{{num}}</span>/<span>100</span></p>
<textarea placeholder="请问有什么问题问老师呢" v-model="text" @input="change"></textarea>
<button :class="[num>0?'active':'']" @click="submit">发布</button>






<Bottom></Bottom>
</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				text:"",
				num:0,
				id:0,
				ConsoledateId:this.$route.params.ConsoledateId
				
			}
			
		},
		created(){
			
			
			
		},
		mounted(){
			
			
		},
		methods:{
			change(e){
				var len=e.target.value.length
				if(len>100){
					this.text=e.target.value.substr(0,101)
				len=100
				
				}
				this.num=len
				
				
			},
			getData(){
				
				var obj={
					"s":this.$store.state.sessionId,
					"id":this.ConsoledateId,
					"parentId":this.id,
					"content":this.text
				}
				
				this.$Api.LeaveWordSubmit(obj).then((res)=>{
					
				})
				
				
				
			},
			submit(){
				if(this.num>0){
					
					this.getData()
				}
				
				
				
			}
			
			
		}
	}
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
p{
	font-size: 0.3rem;
	padding: 0.3rem;
}
textarea{
	width: 6.9rem;
	height: 4.5rem;
	border: 0.02rem solid #CCCCCC;
	resize: none;
    font-size: 0.3rem;
    text-indent: 0.2rem;
    padding-top: 0.2rem;
    box-sizing: border-box;
    border-radius:0.15rem ;
    outline: none;
    margin-left: 0.3rem;
}
button{
	background: #cccccc;
}
.active{
	background: #fa8a12;
}
</style>


